/* slider reset */
.vspec .uncos-slider .slick-track,
.vspec .questions-slider .slick-track,
.vspec .promo-slider .slick-track,
.vspec .case__list .slick-track,
.vspec .glance__slider .slick-track {
	opacity: 1 !important;
	left: auto !important;
	width: auto !important;
	-webkit-transform: none !important;
	-ms-transform: none !important;
	transform: none !important;
}

.vspec .uncos-slider .slick-track::before, .vspec .uncos-slider .slick-track::after,
.vspec .questions-slider .slick-track::before,
.vspec .questions-slider .slick-track::after,
.vspec .promo-slider .slick-track::before,
.vspec .promo-slider .slick-track::after,
.vspec .case__list .slick-track::before,
.vspec .case__list .slick-track::after,
.vspec .glance__slider .slick-track::before,
.vspec .glance__slider .slick-track::after {
	content: none;
}

.vspec .uncos-slider .slick-slide,
.vspec .questions-slider .slick-slide,
.vspec .promo-slider .slick-slide,
.vspec .case__list .slick-slide,
.vspec .glance__slider .slick-slide {
	position: relative !important;
	width: auto !important;
	height: auto !important;
	opacity: 1 !important;
	left: auto !important;
	top: auto !important;
	float: none !important;
	z-index: auto !important;
}

.vspec .uncos-slider .slick-arrow,
.vspec .questions-slider .slick-arrow,
.vspec .promo-slider .slick-arrow,
.vspec .case__list .slick-arrow,
.vspec .glance__slider .slick-arrow {
	display: none !important;
}

.vspec .uncos-slider .slick-dots,
.vspec .questions-slider .slick-dots,
.vspec .promo-slider .slick-dots,
.vspec .case__list .slick-dots,
.vspec .glance__slider .slick-dots {
	display: none !important;
}

/* slider reset end */
@media (min-width: 980px) {
	.nav-opened .vspec .nav-overlay-page {
		display: none;
	}
}

@media (min-width: 768px) {
	.nav-opened .vspec.main-page .nav-overlay-page {
		display: none;
	}
}

@media (max-width: 479px) {
	.vspec .header .nav-overlay-page {
		height: calc(100% + 70px);
		top: -70px;
	}
}

.vspec.inner-page .header {
	position: relative;
	left: auto;
	top: auto;
}

.vspec .header-holder {
	height: auto;
	border: 2px solid currentColor;
	border-width: 0 2px 2px 0;
	padding: 0;
}

