.vspec .logo {
	box-shadow: none;
	border: 0 solid currentColor;
	border-width: 0 2px 2px 2px;
	box-sizing: border-box;
	height: 202px;
}

.vspec.schemecolor-white .logo > a::before,
.vspec.schemecolor-white .logo > span::before {
	-webkit-filter: grayscale(1) contrast(3) invert(0);
	filter: grayscale(1) contrast(3) invert(0);
}

.vspec.schemecolor-blue .logo > a::before,
.vspec.schemecolor-blue .logo > span::before {
	-webkit-filter: grayscale(1) contrast(1) invert(1);
	filter: grayscale(1) contrast(1) invert(1);
}

.vspec.main-page .logo {
	height: 174px;
}

@media (max-width: 979px) {
	.vspec.inner-page .logo {
		height: 80px;
		border: none;
	}
}

@media (max-width: 767px) {
	.vspec .logo {
		height: 80px;
		border: none;
	}
	.vspec.main-page .logo {
		height: 80px;
	}
}

.logo-reduce .vspec .logo {
	height: 200px;
}

.vspec .logo > a:before,
.vspec .logo > span:before,
.vspec .logo > a:after,
.vspec .logo > span:after {
	background-color: transparent;
}

.vspec .logo > a:before,
.vspec .logo > span:before {
	opacity: 1;
	background-image: url(../img/logo.png);
}

.vspec.inner-page .logo > a:after,
.vspec.inner-page .logo > span:after {
	opacity: 0;
}

.vspec .logo > a:after,
.vspec .logo > span:after {
	-webkit-background-size: contain;
	background-size: contain;
	width: calc(100% - 20px);
	left: 10px;
}

@media (max-width: 767px) {
	.vspec .logo > a:before,
	.vspec .logo > span:before {
		opacity: 0;
	}
	.vspec .logo > a:after,
	.vspec .logo > span:after {
		opacity: 1;
	}
}

@media (max-width: 979px) {
	.vspec.inner-page .logo > a:before,
	.vspec.inner-page .logo > span:before {
		opacity: 0;
	}
	.vspec.inner-page .logo > a:after,
	.vspec.inner-page .logo > span:after {
