@import "../libs/include-media/dist/include-media";
@import "utils/variables";
@import "utils/functions";
@import "utils/mixins";
@import "utils/placeholders";

/*=========== SPECIAL =========== */
.hide-page {
	display: none !important; }

body.vspec {
	font-size: $font-size;
	line-height: $line-height;
	//font-family: Tahoma, Geneva, sans-serif
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	//font-family: "Times New Roman", Serif
	color: $spec-text-color;
	background-color: $spec-bg-color; }

body.vspec.fontsize-small {
	font-size: $font-size--sm; }

body.vspec.fontsize-large {
	font-size: $font-size--lg; }

body.vspec.spacing-medium {
	letter-spacing: $letter-spacing--medium; }

body.vspec.spacing-large {
	letter-spacing: $letter-spacing--large; }

body.vspec.schemecolor-white {
	color: $spec-text-color--scheme-white;
	background-color: $spec-bg-color--scheme-white; }

body.vspec.schemecolor-blue {
	color: $spec-text-color--scheme-blue;
	background-color: $spec-bg-color--scheme-blue; }

body.vspec::before {
	content: none; }

.vspec .wrapper,
.vspec .footer {
	padding-left: 70px;
	padding-right: 70px;
	padding-top: 0; }

.inner-page .main > .max-wrap:before {
	display: none; }

@media (max-width: 1439px) {
	.vspec .wrapper,
	.vspec .footer {
		padding-right: 0; } }

@include media('<mob-md') {
	.vspec .wrapper,
	.vspec .footer {
		padding-left: 0; }
	.vspec .wrapper {
		padding-top: 56px; } }

.vspec {
	.max-wrap {
		margin: 0; }

	.content {
		padding-top: 30px;
		padding-bottom: 30px; }

	.article-press {
		padding-top: 30px;
		color: inherit; }

	.press-archive + .press-adt .article-press {
		padding-top: 0; }

	.press {
		margin-top: 0; }

	.full-width {
		margin: 0; }

	.spacer {
		display: none; }

	.footer {
		margin-top: 0 !important; } }

.vspec path,
.vspec polygon {
	fill: currentColor !important; }

/* font */
.vspec .btn-default,
.vspec .btn-default-alt {
	font-family: inherit; }

.vspec {
	h1, h2, h3, h4, h5, h6 {
		color: inherit; } }

.vspec h1 {
	font-size: $title-font-size;
	line-height: $title-line-height;
	letter-spacing: inherit;
	padding: 0;
	margin-bottom: $bottom-space-item; }

.vspec h2 {
	font-size: $sub-title-font-size;
	line-height: $sub-title-line-height;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h3 {
	font-size: $item-font-size;
	line-height: $item-line-height;
	font-weight: 700;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h4 {
	font-size: inherit;
	line-height: inherit;
	font-weight: 700;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h5 {
	font-size: inherit;
	line-height: inherit;
	text-transform: uppercase;
	font-weight: normal;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h6 {
	font-size: inherit;
	line-height: inherit;
	font-weight: 500;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec p {
	letter-spacing: inherit;
	margin-bottom: $bottom-space;
	padding: 0; }

.vspec .to-print {
	display: none; }

.file__info {
	letter-spacing: normal; }

.vspec {
	.img-group {
		display: none; }

	&.imageson .img-group {
		display: block; } }

