/* slider reset */
.vspec {
	.uncos-slider,
	.questions-slider,
	.promo-slider,
	.case__list,
	.glance__slider {
		.slick-track {
			opacity: 1 !important;
			left: auto !important;
			width: auto !important;
			-webkit-transform: none !important;
			-ms-transform: none !important;
			transform: none !important;

			&::before, &::after {
				content: none; } }

		.slick-slide {
			position: relative !important;
			width: auto !important;
			height: auto !important;
			opacity: 1 !important;
			left: auto !important;
			top: auto !important;
			float: none !important;
			z-index: auto !important; }

		.slick-arrow {
			display: none !important; }

		.slick-dots {
			display: none !important; } } }

