		font-size: $item-font-size;
		line-height: $item-line-height;
		text-decoration: underline;
		transition: none; }

	.gallery__title-count {
		font-size: inherit;
		line-height: inherit;
		color: inherit; }

	.gallery__title a:hover {
		.gallery__title-text, .gallery__title-count {
			text-decoration: none;
			color: inherit; } }

	.gallery__previews .slick-arrow,
	.fancybox-custom-btn span {
		color: inherit;
		border-color: currentColor;
		transition: none;
		background-color: $spec-bg-color;

		&::before, &::after {
			content: "";
			display: block;
			color: inherit;
			width: 20px;
			height: 4px;
			margin: 0;
			left: 50%;
			top: 50%;
			background: none;
			background-color: currentColor;
			opacity: 1 !important; }

		&::before {
			transform: rotate(45deg);
			margin: -7px 0 0 -8px; }

		&::after {
			transform: rotate(-45deg);
			margin: 4px 0 0 -8px; } }

	.gallery__previews .slick-prev {
		&::before {
			transform: rotate(-45deg);
			margin: -7px 0 0 -10px; }

		&::after {
			transform: rotate(45deg);
			margin: 4px 0 0 -10px; } }

	&.schemecolor-white {
		.gallery__previews .slick-arrow,
		.fancybox-custom-btn span {
			background-color: $spec-bg-color--scheme-white; } }

	&.schemecolor-blue {
		.gallery__previews .slick-arrow,
		.fancybox-custom-btn span {
			background-color: $spec-bg-color--scheme-blue; } }

	&:not(.mobile-device) {
		.gallery__previews .slick-arrow,
		.fancybox-custom-btn span {
			&:hover {
				background-color: $spec-text-color;
				color: $spec-bg-color; } } }

	&.schemecolor-white:not(.mobile-device) {
		.gallery__previews .slick-arrow,
		.fancybox-custom-btn span {
			&:hover {
				background-color: $spec-text-color--scheme-white;
				color: $spec-bg-color--scheme-white; } } }

	&.schemecolor-blue:not(.mobile-device) {
		.gallery__previews .slick-arrow,
