		text-align: inherit;
		background: none;
		margin: 0 0 $bottom-space;

		&::before {
			content: none; } }

	.error-404__list,
	.user-content .error-404__list {
		font-size: $item-font-size;
		line-height: $item-line-height;
		padding: 0;
		margin-bottom: $bottom-space;

		li {
			display: block;
			padding: 0;
			margin-bottom: round-float($bottom-space/2, 1);

			&::before {
				content: none; } }

		a {
			color: inherit; } } }

/* page 404 end */

